let neo4j = require('neo4j-driver').v1;
let driver = neo4j.driver("bolt://localhost:7687", neo4j.auth.basic("neo", "one"));
let session = driver.session();

let readTxResultPromise = session.readTransaction(function (transaction) {
    transaction.run(ServiceConstants.GET_MOVIES);
});



export default readTxResultPromise;

